import React from 'react'
import './AddPost.css'
import { usuarios } from './Usuarios';

const AddPost = () => {
    // For todays date;
Date.prototype.today = function () { 
    return ((this.getDate() < 10)?"0":"") + this.getDate() +"/"+(((this.getMonth()+1) < 10)?"0":"") + (this.getMonth()+1) +"/"+ this.getFullYear();
}

// For the time now
Date.prototype.timeNow = function () {
     return ((this.getHours() < 10)?"0":"") + this.getHours() +":"+ ((this.getMinutes() < 10)?"0":"") + this.getMinutes() +":"+ ((this.getSeconds() < 10)?"0":"") + this.getSeconds();
}
    let newDate = new Date();
    let datetime = "Fecha y hora: " + newDate.today() + " @ " + newDate.timeNow();
    return (
        <div className="Postea">
            <p>Añada un nuevo post</p>
            <form className="Formulario">
                <label id="etiqueta-autor" htmlFor="author">Autor</label>
                <select className="author" name="author" placeholder="Selecciona un autor">
                    <option className="opcion-author" value="" selected> Elija un autor </option>
                    {usuarios.map((elemento) =>{
                    return (
                        <option className="opcion-author" value={elemento.autor}>{elemento.autor}</option>
                    )})}
                </select>
                <input type="text" name="titulo" id="title" placeholder="Título de la publicación"/>
                <textarea name="post" id="post-nuevo" cols="30" rows="8" placeholder="Escribe aquí el contenido del post" required></textarea>
                <label htmlFor="post" id="fecha-hora">{datetime}</label>
                <button class="btn btn-publicar" type="submit">Publicar</button>
            </form>
        </div>
    )
}

export default AddPost
