import './Publicaciones.css';
import React from 'react';
import { entradas } from './EntradasBlog';

const Publicaciones = () => {
    return (
        <section className="post">
            {entradas.map((item) => {
                return (
                    <div id='post'>
                        <h2 className="titulo-post">{item.title}</h2>
                        <h4 className="autor-post">{item.autor}</h4>
                        <span className="fechahora-post">{item.datetime}</span>
                        <p className="contenido-post">{item.post}</p>
                    </div>
                );
            })}
        </section>
    )
}

export default Publicaciones
